import React, { useCallback } from "react";
import { Button, Form, Select, DatePicker, TimePicker, Input } from "antd";
import dayjs from 'dayjs';
import "../styles/Form.css";

function FormPage() {
  const [form] = Form.useForm();
  const { Option } = Select;
  const format = "HH:mm";
  const { TextArea } = Input;

  const validateMessages = {
    required: "   !",
  };

  const floors = [];
  for (let i = 3; i <= 27; i++) {
    floors.push({
      value: i.toString(),
      label: i.toString(),
    });
  }

  const rooms = [];
  for (let i = 1; i <= 10; i++) {
    rooms.push({
      value: i.toString(),
      label: i.toString(),
    });
  }

  const disabledDate = (current) => {
    return current < dayjs().startOf('day');;
  };

  const handleSubmit = (values) => {
    form
      .validateFields()
      .then((values) => console.log(values))
      .catch((error) => console.log(error));
  };
  const handleReset = useCallback(() => {
    form.resetFields();
  }, [form]);

  return (
    <Form
      form={form}
      name="form"
      autoComplete="off"
      validateMessages={validateMessages}
      initialValues={{ remember: true }}
      className="form"
      onFinish={handleSubmit}
    >
      <h2 className="title">  </h2>
      <Form.Item
        name="tower"
        label=""
        rules={[{ required: true }]}
        className="field_container"
      >
        <Select placeholder=" " className="field">
          <Option value="a_tower">A </Option>
          <Option value="b_tower"> </Option>
        </Select>
      </Form.Item>
      <Form.Item
        name="floor"
        label=""
        rules={[{ required: true }]}
        className="field_container"
      >
        <Select
          placeholder=" "
          options={floors}
          className="field"
        />
      </Form.Item>
      <Form.Item
        name="room"
        label=""
        rules={[{ required: true }]}
        className="field_container"
      >
        <Select
          placeholder=" "
          options={rooms}
          className="field"
        />
      </Form.Item>
      <Form.Item
        name="date"
        label=""
        rules={[{ required: true }]}
        className="field_container"
      >
        <DatePicker placeholder=" " className="field" disabledDate={disabledDate}/>
      </Form.Item>
      <Form.Item
        name="time_interval"
        label=" "
        rules={[{ required: true }]}
        className="field_container"
      >
        <TimePicker.RangePicker
          format={format}
          placeholder={["", ""]}
          className="field"
        />
      </Form.Item>
      <Form.Item name="comment" label="" className="field_container">
        <TextArea
          className="field"
          style={{
            height: 120,
            resize: "none",
          }}
        />
      </Form.Item>
      <div className="buttons_container">
        <Button type="primary" htmlType="submit" className="button">
          
        </Button>
        <Button htmlType="button" onClick={handleReset} className="button">
          
        </Button>
      </div>
    </Form>
  );
}

export default FormPage;

